import React from 'react';
import './about-us.style.css';
import CCard from '../../components/contact-card/contact-card.component';
import { useStore } from 'react-redux';


const AboutUs = () => {

    const store = useStore();
    React.useEffect(() => {
        store.dispatch({ type: 'REMOVE_FOOTER' });
        return () => store.dispatch({ type: 'ADD_FOOTER' });
    }, [])





    const Teams = team.map((value, index) => <CCard key={`teamm${index}`}
        name={value.name} title={value.title} image={value.image} />)

    return (
        <div className='abous-us-container'>
            <h3 style={{
                width: '100%', fontFamily: "Samim", fontSize: '22px', paddingTop: '28px',
                fontWeight: '900', textAlign: 'center'
            }}>     </h3>
            {Teams}
        </div>
    )
}



export default AboutUs;



const team = [
    { name: ' ', title: '    ', image: 'http://sanjesh.love/img/team/team_img1.jpg' },
    { name: ' ', title: '     ', image: 'http://sanjesh.love/img/team/team_img2.jpg' },
    { name: ' ', title: '        ', image: 'http://sanjesh.love/img/team/team_img3.jpg' },
    { name: ' ', title: '     ', image: 'http://sanjesh.love/img/team/team_img4.jpg' },
    { name: ' ', title: '       ', image: 'http://sanjesh.love/img/team/team_img5.jpg' },
    { name: ' ', title: '         ', image: 'http://sanjesh.love/img/team/team_img6.jpg' },
]